//------------ import

//------------ init

document.addEventListener('DOMContentLoaded', _ => {
  
  function runTimer() {
    const second = 1e3
    const container  = document.getElementById('countdownTimer')
    let timerBox = container.firstElementChild
    let c = container.childElementCount - 2;

    container.lastElementChild.style.display = 'none'
    timerBox.style.display = 'block'
    
    const interval = setInterval( _ => {
      !c && clearInterval(interval)

      timerBox.style.display = 'none'
      timerBox = timerBox.nextElementSibling
      timerBox.style.display = 'block'
      c--
    }, second)
  }

  const screens = document.querySelectorAll('.screen')
  let counter = 0
  let screen

  const checkIsCountdown = screen => screen.getAttribute('id') === 'countdown'

  prev.addEventListener('click', _ => {
    counter--
    counter < 0 && (counter = screens.length -1)
    toggleScreen(counter)
    checkIsCountdown(screen) && runTimer()
  })

  next.addEventListener('click', _ => {
    counter++
    counter >= screens.length && (counter = 0)
    toggleScreen(counter)
    checkIsCountdown(screen) && runTimer()
  })

  function toggleScreen(index) {
    for(const el of screens) {
      el.style.display = 'none'
      screen = screens[index]
      screen.style.display = 'block'
    }
    checkIsCountdown(screen) && runTimer()
  }
})





